.container3{display:flex;justify-content:center;align-items:center;text-align:center;padding:3vw;background:url("../../assets/picture/BG/背景-防空洞.png") center/cover no-repeat;height:105vh;width:105vw}.content3{display:flex;flex-direction:column;align-items:center;max-width:90%;margin:0 auto;color:#fff;font-weight:bold;overflow-y:auto;padding:2vh;background-color:rgba(0,0,0,.6);border-radius:1vw}.text3{margin-bottom:2vh;font-size:3vw;color:#f5f5f5;text-shadow:.2vw .2vw .4vw rgba(0,0,0,.7);line-height:1.5}.button3{padding:2vh 4vh;font-size:2.5vh;background-color:#8b5a2b;color:#fff;border:none;border-radius:1.5vh;cursor:pointer;margin-top:3vh;transition:background-color .3s ease,transform .3s ease;box-shadow:0 .4vh .8vh rgba(0,0,0,.2)}.button3:hover{background-color:#a07246;transform:scale(1.1)}.endButton3,.finalButton3{margin-top:4vh}.input3{padding:2vh;font-size:2vh;width:100%;max-width:60vw;margin-bottom:2vh;border:1px solid #ccc;border-radius:.5vh;box-shadow:0 .2vh .4vh rgba(0,0,0,.2)}.alert-toggle{color:#fff;background-color:#a07246;padding:.5em 1em;min-width:50px;min-height:30px;border-radius:10px;box-shadow:0 2px 4px rgba(0,0,0,.25);cursor:pointer;transition:background-color .3s,transform .3s}.alert-toggle:hover{background-color:#b5835a;transform:scale(1.1)}@media(max-width: 1024px){.text3{font-size:4vw}.input3{font-size:2.5vw}.button3{font-size:2.5vw;padding:3vh 5vh}}@media(max-width: 768px){.text3{font-size:5vw}.input3{font-size:3vw}.button3{font-size:3vw;padding:3vh 6vh}}@media(max-width: 480px){.text3{font-size:6vw}.input3{font-size:4vw}.button3{font-size:4vw;padding:4vh 8vh}}